html,
body,
div,
span,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
abbr,
address,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
samp,
small,
strong,
sub,
sup,
var,
b,
i,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

:root {
  --main-color: #2a2929;
  --card-color: #5f545f;
  --sub-color: #3d223d;
  --accent: #987fbb;
  --text-color: #eaeaea;
  --con-color: #77b599;
  --pop-color: #e5bde9;
  --dub-color: #d0afe6;

  --priFont: "Bebas Neue", cursive;
  --secFont: "Kanit", sans-serif;
}

/* Set basic background */
body {
  background-image: url("/img/Nodes.svg");
  color: #eaeaea;
  font-family: "Bebas Neue", cursive;
}

.App {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
/* link color */
a {
  font-family: "Kanit", sans-serif;
  color: #77b599;
  color: var(--con-color);
  text-decoration: none;
}
a:hover {
  color: #eaeaea;
  color: var(--text-color);
}

/* header */
header {
  padding-top: 3%;
}

.navigation {
  width: 100%;
}
header::before {
  content: "";
  top: 3%;
  width: 100%;
  height: 10%;
  -webkit-filter: blur(3rem);
          filter: blur(3rem);
  position: absolute;
  z-index: 0;
  background: #eaeaea;
  background: var(--text-color);
  opacity: 40%;
}
header h1 {
  text-align: center;
  font-size: 2rem;
  color: #77b599;
  color: var(--con-color);
}
header menu {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  position: absolute;
  z-index: 2;
  right: calc(10% / 4);
}
header menu ul {
  list-style-type: none;
  display: flex;
  flex-direction: row;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 100%;
}
header menu ul li {
  text-align: center;
  padding: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.hero {
  background-color: transparent;
  height: 100vh;
  padding-top: -5%;
  position: relative;
  top: 0rem;
  border-top: 0.1rem dashed #d0afe6;
  border-top: 0.1rem dashed var(--dub-color);
  border-bottom: 0.1rem dashed #77b599;
  border-bottom: 0.1rem dashed var(--con-color);
}
/* hero text box */
.hello-box {
  position: absolute;
  top: 30%;
  left: 0%;
  right: 0%;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  text-align: center;
  font-size: 2em;
  background-color: transparent;
  text-align: center;
  flex-direction: row;
}
/* hero text */
.intro {
  font-size: 100%;
  font-weight: 800;
  line-height: 1.4em;
  letter-spacing: 0.8rem;

  overflow: hidden;
  height: 100%;
  margin-inline: auto;
  text-align: center;
  padding: 10%;
  flex-wrap: wrap;
  white-space: wrap;
  -webkit-animation: typing 2s steps(28);
          animation: typing 2s steps(28);
}
.carrot {
  color: #77b599;
  color: var(--con-color);
  -webkit-animation: blinker 1s linear infinite;
          animation: blinker 1s linear infinite;
  -webkit-filter: drop-shadow(0rem 0.3rem 0.3rem #77b599);
          filter: drop-shadow(0rem 0.3rem 0.3rem #77b599);
  -webkit-filter: drop-shadow(0rem 0.3rem 0.3rem var(--con-color));
          filter: drop-shadow(0rem 0.3rem 0.3rem var(--con-color));
}

@-webkit-keyframes blinker {
  0% {
    color: #77b599;
    color: var(--con-color);
  }
  100% {
    color: transparent;
  }
}

@keyframes blinker {
  0% {
    color: #77b599;
    color: var(--con-color);
  }
  100% {
    color: transparent;
  }
}

main {
  width: 100%;
  justify-content: center;
  margin-inline: auto;
  padding-inline: auto;
}

.content-card-style {
  margin-block: 8%;
  color: #77b599;
  color: var(--con-color);
  margin-inline: auto;
  font-family: "Kanit", sans-serif;
  font-family: var(--secFont);
  font-size: 1.3em;
  width: 90%;
  padding: 5%;
  border-radius: 3rem;
  align-items: center;
  justify-content: center;
}
.project-box {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  grid-gap: 2%;
  gap: 2%;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
.space-section {
  padding-block: 3%;
  padding-inline: 1%;
}

.my-card {
  border-radius: 15rem;
  width: 22rem;
  height: 32rem;
  justify-content: center;
  align-items: center;
  margin-block: 1%;
  box-shadow: 0px 0px 10px #d0afe6;
  box-shadow: 0px 0px 10px var(--dub-color);
  color: #eaeaea;
  color: var(--text-color);
  background: linear-gradient(145deg, #3d3c3c, #2e2d2d);
  box-shadow: 31px 31px 70px #1f1e1e, -31px -31px 70px #353434;
  -webkit-backdrop-filter: blur(10rem);
          backdrop-filter: blur(10rem);
}

.my-card hr {
  background-color: #987fbb;
  background-color: var(--accent);
}

.card-text {
  line-height: 1.4rem;
  color: #eaeaea;
  color: var(--text-color);
  display: flex;
  justify-content: center;
  align-items: center;
}
.my-card:hover {
  box-shadow: 0px 0px 18px #eaeaea;
  box-shadow: 0px 0px 18px var(--text-color);
}
.my-card .card-title {
  color: #e5bde9;
  color: var(--pop-color);
  font-weight: 800;
  font-size: 1.7rem;
}
.cards-box {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.site-icon {
  width: 10rem;
  height: auto;
  object-fit: contain;
  width: 100px;
  margin-inline: auto;

  font-size: 6rem;
}
.card-text-box-p {
  margin: 3%;
  line-height: 1.9rem;
  text-align: center;
}
.card-button {
  width: 100%;
  background: transparent;
  border: none;
}
.button-svg {
  width: 50px;
  color: #77b599;
  color: var(--con-color);
  margin-top: 7%;
}
.cardList {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  grid-gap: 2%;
  gap: 2%;
  overflow-wrap: wrap;
  margin-block: 8%;
  margin-inline: auto;
  width: 100%;
}

.card-body {
  flex: 1 1 auto;
  padding: 0rem 3rem;
}
.hide-card {
  opacity: 0;
}

.show-card {
  display: flex;
  -webkit-animation: fadeUp 1s ease-in;
          animation: fadeUp 1s ease-in;
}

@-webkit-keyframes fadeUp {
  0% {
    opacity: 0;
    -webkit-transform: translateY(40px);
            transform: translateY(40px);
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeUp {
  0% {
    opacity: 0;
    -webkit-transform: translateY(40px);
            transform: translateY(40px);
  }
  100% {
    opacity: 1;
  }
}

/* About Section under .about */
.about {
  border-top: 0.1rem dashed #e5bde9;
  border-top: 0.1rem dashed var(--pop-color);
  background-color: #2a2929;
  background-color: var(--main-color);
  text-align: center;
  line-height: 1.2;
  border-bottom: 0.1rem dashed #e5bde9;
  border-bottom: 0.1rem dashed var(--pop-color);
}
.about p {
  color: rgb(226, 239, 232);
  font-size: 1.1rem;
}
.about h2 {
  color: #77b599;
  color: var(--con-color);
}
.about img {
  width: 100%;

  overflow: visible;
  border-radius: 85%;
  margin: 3%;
  box-shadow: 0rem 0rem 2.2rem #eaeaea,
    0rem 0rem 0rem 0.3rem #77b599,
    0rem 0rem 0rem 0.6rem #e5bde9, 0rem 0rem 0rem 0.9rem #987fbb;
  box-shadow: 0rem 0rem 2.2rem var(--text-color),
    0rem 0rem 0rem 0.3rem var(--con-color),
    0rem 0rem 0rem 0.6rem var(--pop-color), 0rem 0rem 0rem 0.9rem var(--accent);
}
.about-img-figure {
  width: 22%;
  height: 22%;
}
.about-box {
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  justify-content: center;
  align-items: center;
}
.about ul li {
  list-style-type: decimal;
}

.my-about-img {
  background-image: url("/img/me.png");
}

/* FOOTER */
/* keep at bottom */
footer {
  border-top: 0.1rem dashed #77b599;
  border-top: 0.1rem dashed var(--con-color);
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  padding-block: 2%;

  color: #e5bde9;

  color: var(--pop-color);
  justify-content: center;
  -webkit-filter: drop-shadow(0rem 0.3rem 0.3rem #e5bde9);
          filter: drop-shadow(0rem 0.3rem 0.3rem #e5bde9);
  -webkit-filter: drop-shadow(0rem 0.3rem 0.3rem var(--pop-color));
          filter: drop-shadow(0rem 0.3rem 0.3rem var(--pop-color));
  background-color: #2a2929;
}

.footerLinksList {
  width: 80%;

  display: flex;
  list-style: none;
  justify-content: space-evenly;
  align-items: flex-end;
}
/* screens above */
@media (min-width: 768px) {
  .about-box {
    flex-direction: row;
  }

  .left-box {
    flex-direction: row;
  }
  .right-box {
    flex-direction: row;
  }
  .hello-box p {
    font-size: 180%;
  }
}

